<template>
  <div class="follow-wp">
    <div class="follow-hd">
      <el-button class="mr10" type="primary" @click="addFollowDialog = true"></el-button>
      <el-button class="mr10" type="primary" @click="qryAllSchoolInfo"></el-button>
      <el-button type="primary" @click="handleAddOrder"></el-button>
    </div>

    <div class="follow-bd mt20">
      <el-tabs v-model="logTabName" type="card" @tab-click="handleTabClick">
        <el-tab-pane label="" name="followList">
          <follow-list :follow-list="followList" :add-friend-list="addFriendList" :update-list="updateList"></follow-list>
        </el-tab-pane>

        <el-tab-pane label="" name="advanceList">
          <follow-advance :advance-list="advanceList"></follow-advance>
        </el-tab-pane>

        <el-tab-pane label="" name="orderList">
          <follow-order :order-list="orderList"></follow-order>
        </el-tab-pane>

        <el-tab-pane label="" name="callList">
          <follow-call :call-list="callList"></follow-call>
        </el-tab-pane>

        <el-tab-pane label="" name="chatList">
          <follow-chat :chat-list="chatList"></follow-chat>
        </el-tab-pane>

        <el-tab-pane label="" name="allocateList">
          <follow-allocate :allocate-list="allocateList"></follow-allocate>
        </el-tab-pane>
      </el-tabs>

    </div>

    <!--   -->
    <el-dialog class="stick" title="" :visible.sync="addFollowDialog"  width="760px">
      <el-form :model="followUp" :rules="rules" ref="followUp" :inline="true" label-width="100px" class="demo-ruleForm">
        <el-form-item label="" prop="followTimeMinute">
          <el-date-picker
            v-model="followUp.followTimeMinute"
            type="datetime"
            placeholder=""
            value-format="yyyy-MM-dd HH:mm:ss"
            default-time="12:00:00">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="followMode">
          <el-select v-model="followUp.followMode" placeholder="">
            <el-option
            v-for="item in wordbook.followMode"
            :key="item.id"
            :label="item.label"
            :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="isConnect" v-if="followUp.followMode == 1 || followUp.followMode == 0">
          <el-select v-model="followUp.isConnect" placeholder="" @change="playCallResult($event)">
            <el-option
              v-for="item in wordbook.isConnect"
              :key="item.id"
              :value="item.id" :label="item.label"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="isValid">
          <el-select v-model="followUp.isValid" placeholder="">
            <el-option
              v-for="item in wordbook.isValid"
              :disabled="followUp.followMode == 1 || followUp.followMode == 0"
              :key="item.id"
              :value="item.id"
              :label="item.label"
            ></el-option>
          </el-select>
        </el-form-item>
        <br>
        <el-form-item label="" >
          <el-input  type="textarea" v-model="followUp.remark" style="width:270%"></el-input>
        </el-form-item>
        <!-- <current title=""></current> -->
        <div class="g-title">
          <h2></h2>
        </div>

        <el-form-item label="" style="width:100%;">
          <el-select v-model="followUp.backPlan" placeholder="">
            <el-option
            v-for="item in wordbook.backPlan"
            :key="item.id"
            :value="item.id"
            :label="item.label">
            </el-option>
          </el-select>
          <el-date-picker
            v-model="followUp.backTimeMinute"
            v-if="followUp.backPlan !== 4"
            type="datetime"
            placeholder=""
            value-format="yyyy-MM-dd HH:mm:ss"
            default-time="12:00:00">
          </el-date-picker>
        </el-form-item>
        <br>
        <el-form-item label=""  >
          <el-input  type="textarea" v-model="followUp.backRemark" style="width:270%"></el-input>
        </el-form-item>

        <el-form-item style=" width:100%; text-align:center; "  >
          <el-button @click="addFollowDialog = false"></el-button>
          <el-button type="success" @click="followUprEnter('followUp')"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <!--  -->
    <el-dialog class="stick" title="" :visible.sync="schoolDialog"  width="760px">
      <el-form :model="reservations" :rules="reservationsRules" ref="reservations" :inline="true" label-width="100px" class="demo-ruleForm">
        <el-form-item label="" prop="advanceTimeMinute">
          <el-date-picker
            v-model="reservations.advanceTimeMinute"
            type="datetime"
            placeholder=""
            value-format="yyyy-MM-dd HH:mm:ss"
            default-time="12:00:00">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="schoolIdFk">
          <el-select v-model="reservations.schoolIdFk" placeholder="">
            <el-option
            v-for="item in schoolList"
            :key="item.id"
            :value="item.id"
            :label="item.name"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="proIdFk">
          <el-select v-model="reservations.proIdFk" placeholder="" @change="getProject($event)">
            <el-option
            v-for="item in projectOptions"
            :key="item.proId"
            :value="item.proId"
            :label="item.proName"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="ctIdFk">
          <el-select v-model="reservations.ctIdFk" placeholder="">
            <el-option
            v-for="item in projectClassOptions"
            :key="item.ctId"
            :value="item.ctId"
            :label="item.cname">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="">
          <el-input v-model="reservations.price"></el-input>
        </el-form-item>
        <br>
        <el-form-item label=""  >
          <el-input  type="textarea" v-model="reservations.remark"></el-input>
        </el-form-item>
        <el-form-item style=" width:100%; text-align:center; "  >
          <el-button @click="schoolDialog = false"></el-button>
          <el-button type="success" @click="reservationsEnter('reservations')"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <el-dialog class="stick" title="" :visible.sync="addOrderDialog"  width="760px">
      <el-form :model="orderForm" :rules="createOrderVerification" ref="orderForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="" prop="name">
          <el-input v-model="orderForm.name"></el-input>
        </el-form-item>

        <el-form-item label="" style="width:100%;">
          <el-select  placeholder="" style="width:35%;" v-model="orderForm.cardType">
            <el-option v-for="(item,index) in idCardOptions" :label="item.label" :value="item.id" :key="index"></el-option>
          </el-select>
          <el-input  style="width:45%;" v-model="orderForm.cardNo"></el-input>
        </el-form-item>
        <div>
          <el-form-item label="">
            <el-cascader
              :options="province"
              @change="handleProvinceChange($event)"
              v-model="sellCity"
              :change-on-select="true"
              clearable
              :props="provinceProps">
            </el-cascader>
          </el-form-item>
        </div>

        <el-form-item label="" prop="proIdFk">
          <el-select v-model="orderForm.proIdFk" placeholder="" @change="getProject($event)">
            <el-option
            v-for="item in projectOptions"
            :key="item.proId"
            :value="item.proId"
            :label="item.proName"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="ctIdFk" style="width:100%;">
          <el-select v-model="orderForm.ctIdFk" placeholder="">
            <el-option
            v-for="item in projectClassOptions"
            :key="item.ctId"
            :value="item.ctId"
            :label="item.cname">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="" prop="price">
          <el-input v-model="orderForm.price" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="offsetAmount">
          <el-input v-model="orderForm.offsetAmount"></el-input>
        </el-form-item>
        <el-form-item label="" style="width:100%;">
          <span>{{orderForm.price - offsetAmount}}</span>
        </el-form-item>
        <div class="tac">
          <el-button @click="addOrderDialog = false"></el-button>
          <el-button type="success" @click="handleOrderSubmit('orderForm')"></el-button>
        </div>>
      </el-form>
    </el-dialog>

  </div>
</template>

<script>
import { mapState } from 'vuex'
import FollowList from './follow-list'
import FollowAdvance from './follow-advance'
import FollowOrder from './follow-order'
import FollowCall from './follow-call'
import FollowChat from './follow-chat'
import FollowAllocate from './follow-allocate'
import { isidcard, isGAT } from "@/common/util";
import { idCardOptions, isItConnected } from "./data"
export default {
  props: ['stuId', 'student', 'projectOptions'],
  components: {
    FollowList,
    FollowAdvance,
    FollowOrder,
    FollowCall,
    FollowChat,
    FollowAllocate,
  },
  data() {
      //   0
      var validateDiscountAmount = (rule, value, callback) => {
        if (this.orderForm.offsetAmount <= 0) {
          callback(new Error('0'));
        }
        else if(parseInt(this.orderForm.offsetAmount) >= parseInt(this.orderForm.price)) {
          callback(new Error(''));
        }
        else {
          callback()
        }
      }
      return {
        logTabName: 'followList',
        idCardOptions,
        followList: [],
        addFriendList: [],
        updateList: [],
        // 
        addFollowDialog: false,
        schoolDialog: false,
        addOrderDialog: false,
        // 
        projectClassOptions: [],
        // 
        schoolList: [],
        advanceList: [],  // 
        callList: [],
        chatList: [],
        // 
        orderList: [],  // 
        allocateList: [],  // 
        province: [],
        provinceProps: {
          value: 'id',
          label: 'city',
          children: 'cities'
        },
        // 
        sellCity:[],
        // 
        wordbook:{
          followMode: [
            {id: 0, label: ""},
            {id: 1, label: ""},
            {id: 2, label: ""},
            {id: 3, label: "QQ"}
          ],
          isConnect: isItConnected,
          isValid: [
            {id: 1, label: ""},
            {id: 0, label: ""}
          ],
          backPlan: [
            {id: 0, label: ""},
            {id: 1, label: ""},
            {id: 2, label: ""},
            {id: 3, label: ""},
            {id: 4, label: ""}
          ],
          mode:['','','']
        },
        followUp:{
          followTimeMinute: "",
          followMode: "",
          isConnect: "",
          isValid: "",
          remark: "",
          backPlan: "",
          backTimeMinute: "",
          backRemark: ""
        },
        //  
        reservations:{
          advanceTimeMinute: "",
          schoolIdFk: "",
          proIdFk: "",
          ctIdFk: "",
          price: "",
          remark: ""
        },
        //  
        orderForm:{
          name: "",
          cardType: "",
          cardNo: "",
          regionIdFk: "",
          proIdFk: "",
          ctIdFk: "",
          offsetAmount: "",
          price: ""
        },
        rules:{
          followTimeMinute: [
              { required: true, message: '', trigger: 'change' }
            ],
          followMode: [
              { required: true, message: '', trigger: 'change' }
          ],
          isConnect: [
              { required: true, message: '', trigger: 'change' }
          ],
          isValid: [
              { required: true, message: '', trigger: 'change' }
          ]
        },
        // 
        reservationsRules:{
          advanceTimeMinute: [
              { required: true, message: '', trigger: 'change' }
            ],
          schoolIdFk: [
              { required: true, message: '', trigger: 'change' }
          ],
          proIdFk: [
              { required: true, message: '', trigger: 'change' }
          ],
          ctIdFk: [
              { required: true, message: '', trigger: 'change' }
          ]
        },
        // 
        createOrderVerification:{

          proIdFk: [
              { required: true, message: '', trigger: 'change' }
          ],
          ctIdFk: [
              { required: true, message: '', trigger: 'change' }
          ],
          offsetAmount: [
            {validator: validateDiscountAmount, trigger: 'blur'}
          ]
        },

      }
    },
    computed: {
      ...mapState({
        userInfo: state => state.user.userInfo
      }),
      offsetAmount() {
        return Math.round(this.orderForm.offsetAmount * 100) / 100
      }
    },
    watch: {
      '$route': 'refreshRoute',
      followUp: {
        handler(val, oldVal) {
          console.log(val)
          if (val && val.backPlan == 4) {
            delete this.followUp.backTimeMinute
          }
        },
        deep:true
      }
    },
    created (){
      this.refreshRoute();
      this.qryArea();
    },
    methods: {
      handleTabClick(tab, event) {
        // 
        if(this.logTabName == "advanceList"){
          this.qryStudentAdvanceList()
        }
        // 
        else if(this.logTabName == "orderList"){
          this.qryOrderList()
        }
        // 
        else if(this.logTabName == "callList"){
          this.getStuCallDetailList()
        }
        // 
        else if(this.logTabName == "chatList"){
          this.API.getStuOnlineChatDetailList({stuId: this.stuId}).then((res)=>{
            this.chatList = res.list
          });
        }
        // 
        else if(this.logTabName == "allocateList"){
          this.qryStudentAllocateList()
        }
      },
      // 
      refreshRoute(){
        // 
        this.getStuFollowDetailList();
        // -
        this.qryStudentFriendList();
        // - -
        this.qryStudentMdfLogList();
        this.getStuCallDetailList();
      },
      getStuCallDetailList(){
        this.API.getStuCallDetailList({stuId: this.stuId}).then((res) => {
          console.log(res)
          this.callList = res.list;
        });
      },
      // 
      getStuFollowDetailList(){
        this.API.getStuFollowDetailList({
          stuId: this.stuId
        }).then(res=>{
          this.followList = res.list;
        }).catch(err => {
          console.log(err.message)
        });
      },
      // 
      followUprEnter(formName){
        this.$refs[formName].validate((valid) => {
          if(!valid) return;
          //  
          if(this.followUp.backPlan == 4){
            this.followUp.backTimeMinute = ""
          }

          this.followUp.stuIdFk = this.stuId;
          this.API.addStudentFollowDetail(this.followUp)
          .then(res =>{
            this.$message({
              message: '',
              type: 'success'
            });
            this.addFollowDialog = false;
            this.$router.go(0)
          }).catch(err => {
            console.log(err.message)
          });

        })
      },
      // -
      qryStudentFriendList(){
        this.API.qryStudentFriendList({
          stuId: this.stuId
        })
        .then(res =>{
          // console.log(res);
          this.addFriendList = res.list;
        }).catch(err => {
          console.log(err.message)
        });
      },
      // - -
      qryStudentMdfLogList(){
        this.API.qryStudentMdfLogList({
          stuId: this.stuId
        })
        .then(res =>{
          console.log(res);
          this.updateList = res.list;
        }).catch(err => {
          console.log(err.message)
        });
      },
      // 
      reservationsEnter(formName){
        this.$refs[formName].validate((valid) => {
          if(!valid) return;

          this.reservations.stuIdFk = this.stuId;
          this.API.addStudentAdvance(this.reservations).then(res =>{
            this.$message({
              message: '',
              type: 'success'
            })
            this.schoolDialog = false;
          })
          .catch(err => {
            console.log(err.message)
          });
        })
      },
      // 
      qryAllSchoolInfo(){
        this.schoolDialog = true
        !this.schoolList.length && this.API.qryAllSchoolInfo().then(res =>{
          console.log(res.list);
          this.schoolList = res.list || [];
        }).catch(err => {
          console.log(err.message)
        });
      },
      //  
      qryStudentAdvanceList(){
        this.API.qryStudentAdvanceList({
          stuId: this.stuId
        }).then(res =>{
          this.advanceList = res.list
        }).catch(err => {
          console.log(err.message)
        });
      },
      //  
      handleAddOrder(){
        const student = this.student
        this.addOrderDialog = true;
        this.orderForm.name =  student.name
        this.orderForm.cardType =  student.cardType
        this.orderForm.cardNo =  student.cardNo
        /*this.API.getStuBaseInfo({
          stuId:this.stuId
        })
        .then(res=>{
          let stuInfo = res.result[0].stuInfo;
          this.orderForm.name =  stuInfo.name;
          this.orderForm.cardType =  stuInfo.cardType;
          this.orderForm.cardNo =  stuInfo.cardNo;
        }).catch(err => {
          console.log(err.message)
        });*/
      },
      //
      handleOrderSubmit(formName){
        if (this.orderForm.cardType == 1){
          if (!isidcard(this.orderForm.cardNo)) {
            this.$message({
              message: '',
              type: 'warning'
            })
            return;
          }
        }
        else if (this.orderForm.cardType == 2){
          if (!isGAT(this.orderForm.cardNo)) {
            this.$message({
              message: '',
              type: 'warning'
            })
            return;
          }
        }
        this.$refs[formName].validate((valid) => {
          if(!valid) return;
          this.orderForm.stuIdFk = this.stuId;
          this.API.addOrder(this.orderForm)
          .then(res =>{
            this.$message({
              message: '',
              type: 'success'
            })
            this.addOrderDialog = false;
          })
          .catch(err => {
            console.log(err.message)
          });

        })
        //this.orderForm.dialogFormVisible = false;
      },
      // 
      qryOrderList(){
        this.API.qryOrderList({
          stuId: this.stuId
        }).then(res =>{
          //console.log(res);
          this.orderList = res.list;
        }).catch(err => {
          console.log(err.message)
        });
      },
      // 
      qryStudentAllocateList(){
        this.API.qryStudentAllocateList({
          stuId: this.stuId
        }).then(res =>{
          //console.log(res);
          this.allocateList = res.list;
        }).catch(err => {
          console.log(err.message)
        });
      },
      // 
      getProject(id){
        this.API.qryAllClassType({
          proIdFk:id
        })
        .then(res =>{
          this.projectClassOptions = res.list;
        })
      },
      //  
      playCallResult(id){
        //console.log(id);
        if(id == 0){
          this.followUp.isValid = 1;
        }
        else{
          this.followUp.isValid = 0;
        }
      },
      // 
      qryArea(val){
        this.API.qryArea({areaId: 1}).then((res) => {
          let data = res.result.map((item) => {
            return {
              id: item.id,
              city: item.province || item.city,
              cities: []
            }
          });
          this.province = data;
        })
      },
      // 
      handleProvinceChange(val,key) {
        const province = this.province;
        this.API.qryArea({areaId: val[0] || val}).then((res) => {
          province.forEach((item) => {
            if (item.id == val[0] || item.id == val) {
              if(res.result[0].city){
                item.cities = res.result;
              }else {
                item.cities='';
              }
              return false;
            }

          })
        })
        //  ID
        if(this.sellCity[1]){
          this.orderForm.regionIdFk = this.sellCity[1]
        }
        else{
          this.orderForm.regionIdFk = this.sellCity[0]
        }
      },
    }
}
</script>

<style lang="scss" scoped>
  .follow-wp {
    margin: 20px 0 20px 20px;
    .follow-hd {
      //padding: 15px;
    }
    .follow-bd {
      min-height: 200px;
      background: #fff;
    }
    .box-card{
      margin-bottom: 20px;
      .el-card__header{
        padding: 10px 20px;
      }
      .list{
        padding: 10px 20px 20px 20px;
        overflow: hidden;
        border-bottom: 1px solid #f4f4f4;
        &.orderList{
          li{
            width: 50%;
          }
        }
        li{
          float: left;
          overflow: hidden;
          width: 100%;
          text-align: left;
          line-height: 40px;
          span{
            color: #2c2f33;
          }
          strong{
            color: #848e99;
          }
        }
      }
      .clearfix{

        .title{
          font-size: 14px;
          strong{
            color: #848e99;
            padding-left: 3px;
          }
        }
        .right{
          float: right; padding: 3px 0;
          color:#848e99;
          font-size: 13px;
        }
      }
    }
    .stick{
      .el-form-item{
        width: 45%;
        display: inline-block;
      }
    }
  }
</style>
<style lang="scss">
.follow-up{
  .box-card{
    .el-card__header{
      padding: 10px 20px;
    }
    .el-card__body{
      padding:10px 10px;
    }
  }
}
</style>



