<template lang="html">
  <div class="contacts">
    <div class="item"
         v-for="(item,index) in contactsList"
         :class="[cardClass[index], {period: item.more.length ? false : true}]"
         :key="index"
         @mouseover="_hovmouseover(index,item)"
         @mouseout="_hovmouseout(index,item)"
         @click="_add(item)">
      <template>
        <div class="warp">
          <div class="left">
            <h1>{{contactsName[item.type]}}</h1>
            <div class="next" @click="_next(item)" v-show="item.more.length > 1 ">
              <i class="el-icon-arrow-right"></i>
            </div>
          </div>
          <div
              :class="item.more.length ? 'playcall' : ''"
               v-show="item.playCallToogl"
               v-if="deptInfo.type == 2 && !item.islogin"
               @click='playcall(item.more[item.urIndex],item)'>
            <el-button class="button" size="small" icon="el-icon-service"></el-button>
          </div>
        </div>
        <transition name="fade">
          <strong>{{item.more[item.urIndex]}}</strong>
        </transition>
      </template>
      <template>
        <!--  -->
        <div class="login" v-show="deptInfo.type == 2 && item.islogin == true">
          <p></p>
          <el-button @click="reLogin()" type="primary" size='mini'></el-button>
        </div>
      </template>
    </div>

    <!--   -->
    <el-dialog class="addAlert" title="" :visible.sync="dialogFormVisible" width="38%">
      <el-form :model="addAlert" :rules="rules" ref="addAlert" label-width="100px" class="demo-ruleForm">
        <el-form-item style="margin-left:-100px">
          <el-radio-group v-model="addAlert.text">
            <el-radio-button label=""></el-radio-button>
            <el-radio-button label=""></el-radio-button>
            <el-radio-button label=""></el-radio-button>
            <el-radio-button label="QQ"></el-radio-button>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="tel" v-if="addAlert.text == ''">
          <el-input v-model="addAlert.tel" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="camear" v-if="addAlert.text == ''">
          <el-input v-model="addAlert.camear" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="weixin" v-if="addAlert.text == ''">
          <el-input v-model="addAlert.weixin" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="QQ" prop="QQ" v-if="addAlert.text == 'QQ'">
          <el-input v-model="addAlert.QQ" placeholder="QQ"></el-input>
        </el-form-item>
        <el-form-item style="margin-left:-100px">
          <el-button @click="dialogFormVisible = false"></el-button>
          <el-button type="success" @click="_addEnter('addAlert')"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <!---->
    <el-dialog center :visible.sync="showTip" width="500px" :show-close="false">
      <span slot="title" class="title-header left"></span>
      <span></span>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="overCall" :disabled="btnDisabled">{{callMessage}}</el-button>
      </span>
    </el-dialog>

    <!---->
    <el-dialog
      center
      :visible.sync="showCallDetail"
      width="700px">
      <span slot="title" class="title-header left">-</span>
      <el-form ref="addCallForm" :model="CallFormData" label-suffix="" label-width="110px" :rules="callRule">
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="nick">
              <el-input v-model="CallFormData.nick" placeholder=""></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="sex">
              <el-radio-group v-model="CallFormData.sex">
                <el-radio-button label="1"></el-radio-button>
                <el-radio-button label="2"></el-radio-button>
              </el-radio-group>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="chanceArea">
              <el-cascader :options="areaoptions"
                           v-model="CallFormData.chanceArea" style="width: 100%" clearable class="full-width" clearable
                           :props="areaProps" @change="handleItemChange" :change-on-select="true">
              </el-cascader>
            </el-form-item>
          </el-col>
          <!--<el-col :span="12">
            <el-form-item label="" prop="followMode">
              <el-select v-model="CallFormData.followMode" placeholder=""  class="full-width" clearable>
                <el-option
                  v-for="item in contactType"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>-->
        </el-row>
        <h3 class="title-header margin-bottom-10"></h3>
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="followTimeMinute">
              <el-date-picker
                v-model="CallFormData.followTimeMinute"
                type="datetime"
                value-format="yyyy-MM-dd HH:mm"
                format="yyyy-MM-dd HH:mm"
                disabled
                placeholder="">
              </el-date-picker>
            </el-form-item>
          </el-col>
          <!--<el-col :span="12">
            <el-form-item label="" prop="followMode">
              <el-select v-model="CallFormData.followMode" placeholder=""  class="full-width" clearable>
                <el-option
                  v-for="item in contactType"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>-->
        </el-row>
        <el-form-item label="" prop="remark">
          <el-input type="textarea" v-model="CallFormData.remark"></el-input>
        </el-form-item>
        <h3 class="title-header margin-bottom-10"></h3>
        <el-form-item label="" prop="backPlan">
          <el-col :span="11">
            <el-select v-model="CallFormData.backPlan" placeholder=""  class="full-width" clearable>
              <el-option
                v-for="item in EditReturnLis"
                :key="item.id"
                :label="item.label"
                :value="item.id">
              </el-option>
            </el-select>
          </el-col>
          <el-col :span="11" v-show="CallFormData.backPlan!=4" class="margin-left-10">
            <el-date-picker
              v-model="CallFormData.backTimeMinute"
              type="datetime" style="width: 100%"
              value-format="yyyy-MM-dd HH:mm"
              format="yyyy-MM-dd HH:mm"
              placeholder="">
            </el-date-picker>
          </el-col>
        </el-form-item>
        <el-form-item label="">
          <el-input type="textarea" v-model="CallFormData.backRemark"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
      <el-button type="primary" @click="saveCallDetail"></el-button>
    </span>
    </el-dialog>

    <!---->
    <el-dialog
      center
      :visible.sync="showInvalid"
      width="500px">
      <span slot="title" class="title-header left"></span>
      <el-form ref="invalidForm" :model="invalidData" label-suffix="" label-width="110px" :rules="invalidRule">
        <el-form-item label="" prop="isConnect">
          <el-select v-model="invalidData.isConnect" placeholder=""  class="full-width" clearable>
            <el-option
              v-for="item in invalidType"
              :key="item.id"
              :label="item.label"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="remark">
          <el-input type="textarea" v-model="invalidData.remark"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
      <el-button type="primary" @click="saveInvalid"></el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import { mapState, mapActions } from 'vuex';
  import {isPhone, isMobile, isQQ, isWeixin, formatDate} from "@/common/util.js";
  import { followOptions, isItConnected } from './data.js';
  export default {
    props: ['stuId'],
    data() {
      //  
      var validateTel = (rule, value, callback) => {
        if (!isMobile(value)) {
          callback(new Error(''));
        }
        else {
          callback()
        }
      }
      //  
      var validateCamear = (rule, value, callback) => {
        if (!isPhone(value)) {
          callback(new Error(''));
        }
        else {
          callback()
        }
      }
      //  QQ
      var validateQQ = (rule, value, callback) => {
        if (!isQQ(value)) {
          callback(new Error('QQ'));
        }
        else {
          callback()
        }
      }
      //  
      var validateWeixin = (rule, value, callback) => {
        if (!isWeixin(value)) {
          callback(new Error(''));
        }
        else {
          callback()
        }
      }
      return {
        areaoptions:[],
        areaProps: {
          value: 'id',
          label: 'city',
          children: 'cities'
        },
        showInvalid:false,
        invalidData:{
          isConnect:'',
          remark:'',
          isValid:0,
          followMode:''
        },
        invalidType:[
          {id:0,label:""},
          {id:1,label:""},
          {id:2,label:""},
          {id:3,label:"/"}
        ],
        EditReturnLis: followOptions,
        showCallDetail:false,//modal;
        callRule:{
          followTimeMinute:[{required: true, message: ''}],
          backPlan:[{required: true, message: ''}],
          nick:[{required: true, message: ''}],
          sex:[{required: true, message: ''}],
          chanceArea:[{required: true, message: ''}],
        },
        invalidRule:{
          reason:[{required: true, message: ''}],
        },
        CallFormData:{
          followTimeMinute:formatDate((new Date().getTime())/1000),
          followMode:'',
          remark:'',
          backPlan:'',
          backTimeMinute:'',
          backRemark:'',
          isValid:1,//
          isConnect:1,
          chanceArea:[],
          nick:'',
          sex:''
        },
        contactType:[{
          label:'',
          value:0
        },{
          label:'',
          value:1
        }],
        islogin: false, //    0 || 12 ih
        cardClass: ["back_tel", "back_tel2", "back_wexin", "back_qq", "back_add"], // 
        addAlert: {
          text: "", // 
          tel: "",
          camear: "",
          weixin: "",
          QQ: ""
        },
        dialogFormVisible: false,
        // 
        contactsName: ['', '', '', 'QQ',],
        contactsList: [],
        rules: {
          tel: [
            {validator: validateTel, trigger: 'blur'}
          ],
          camear: [
            {validator: validateCamear, trigger: 'blur'}
          ],
          weixin: [
            {validator: validateWeixin, trigger: 'blur'}
          ],
          QQ: [
            {validator: validateQQ, trigger: 'blur'}
          ]
        },
        callId: '',//id
        showTip: false,
        callMessage: '',
        btnDisabled: true
      }
    },
    created() {
      this.qryStuContact()
      this.isloginEvent()
    },
    watch:{
      '$route': 'qryStuContact',
      studentInfo(){
        const {studentInfo}=this;
        this.CallFormData.nick=studentInfo.nick;
        this.CallFormData.sex=studentInfo.sex;
        this.CallFormData.chanceArea=[studentInfo.provinceIdFk,studentInfo.cityIdFk];
        this.qryArea();
      }
    },
    computed: {
      ...mapState({
        userInfo: state => state.user.userInfo,
        deptInfo: state => state.user.deptInfo,
        // code012
        code: state => state.user.code,
        studentInfo:state => state.workbench.studentInfo,
      })
    },
    methods: {
      ...mapActions(['USER_CLEAR']),
      reLogin() {
        this.USER_CLEAR().then(res => {
          this.$router.replace('/login')
        })
      },
      qryArea(val){
        this.API.qryArea({areaId: 1}).then((res) => {
          let data = res.result.map((item) => {
            return {
              id: item.id,
              city: item.province || item.city,
              cities: []
            }
          });
          this.areaoptions = data;
          if(this.studentInfo['cityIdFk']){
            this.handleItemChange([this.studentInfo.provinceIdFk]);
          }
        })
      },
      // 
      handleItemChange(val) {
        if (val.length > 1 || !val.length) return;
        const {areaoptions} = this;
        this.API.qryArea({areaId: val[0]}).then((res) => {
          areaoptions.forEach((item) => {
            if (item.id == val[0]) {
              if (res.result[0].city) {
                item.cities = res.result;
              } else {
                item.cities = '';
              }
              return false;
            }
          })
        })
      },
      //
      saveInvalid(){
        this.$refs['invalidForm'].validate((valid) => {
          if(valid){
            const data=Object.assign({},this.invalidData,{id:this.callId,stuIdFk:this.studentInfo.stuId});
            this.API.mdfStudentFollowDetail(data).then((res)=>{
              this.$message({
                type: 'success',
                message: '!'
              });
              this.showInvalid=false;
            });
          }
        })
      },
      //
      saveCallDetail(){
        this.$refs['addCallForm'].validate((valid) => {
          if(valid){
            const {chanceArea}=this.CallFormData;
            const data=Object.assign({},this.CallFormData,{id:this.callId,stuIdFk:this.studentInfo.stuId,provinceIdFk:chanceArea[0],cityIdFk:chanceArea[1]||''})
            this.API.mdfStudentFollowDetail(data).then((res)=>{
              this.$message({
                type: 'success',
                message: '!'
              });
              this.showCallDetail=false;
            });
          }
        })
      },
      // 
      qryStuContact() {
        // ID
        //this.stuId = this.$router.currentRoute.query.stuId
        this.API.qryStuContact({
          stuId: this.stuId
        }).then(res => {
            this.contactsList = res.result;
            //  
            let obj = {name: "", urIndex: 0, more: []}
            this.contactsList.push(obj)
          }).catch(err => {
            console.log(err.message)
        });
      },
      // 
      isloginEvent(){
        const code = localStorage.getItem('code');
        console.log(code);
        if(code == 12 || code == 0){
          this.islogin = true;
        }
        else{
          this.islogin = false;
        }
      },
      // 
      _next(item) {
        item.urIndex += 1;
        item.urIndex >= item.more.length ? item.urIndex = 0 : item.urIndex;
      },
      // 
      _add(item) {
        if (!item.name) return;
        //console.log('');
        this.dialogFormVisible = true;
      },
      playcall(tel,item) {
        //
        const code = localStorage.getItem('code');
        const that=this;
        if (code == 0 || code == 12) {
          this.$confirm('' + tel + '?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            //
            this.CallFormData.followMode=item.type;
            this.invalidData.followMode=item.type;
            this.CallFormData.followTimeMinute=formatDate((new Date().getTime())/1000);//
            this.API.hangup({
              userName: this.userInfo.userName,
              mobile: tel,
              studentId: this.stuId
            }).then((res) => {
              this.callId = res.id;
              this.showTip = true;
              let countNum=20;
              let t=setInterval(()=>{
                countNum--;
                that.callMessage=''+countNum+'s'
                if(countNum===0){
                  clearInterval(t);
                  that.btnDisabled=false;
                  that.callMessage='';
                }
              }, 1000)
            });
          });
        } else {
          this.$message({
            type: 'warning',
            message: '!'
          });
        }
      },
      overCall() {
        this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.API.effective({id:this.callId}).then((res)=>{
            if(res.code==1){
              //
              console.log('');
              this.showCallDetail=true;
              this.showTip=false;
            }else{
              //
              console.log('');
              this.showInvalid=true;
              this.showTip=false;
            }
          })
        });
      },
      // 
      _hovmouseover(index, item) {
        if(index == 0 || index == 1){
          if (!this.islogin) { // 
            //item.islogin = true;
            this.$set(this.contactsList[index], 'islogin', true)
          }
        }

      },
      // 
      _hovmouseout(index, item) {
        if(index == 0 || index == 1){
          if (!this.islogin) { // 
            //item.islogin = true;
            this.$set(this.contactsList[index], 'islogin', false)
          }
        }
      },
      // 
      _addEnter(formName) {
        this.$refs[formName].validate((valid) => {
          if (!valid) return;
          // 
          let val = this.addAlert.text;
          if (val == "") {
            this._addEnterMethods(0, this.addAlert.tel);
          }
          else if (val == "") {
            this._addEnterMethods(1, this.addAlert.camear);
          }
          else if (val == "") {
            this._addEnterMethods(2, this.addAlert.weixin);
          }
          else if (val == "QQ") {
            this._addEnterMethods(3, this.addAlert.QQ);
          }

        });
      },
      //   
      _addEnterMethods(index, value) {

        var obj = this.contactsList[index];
        if (obj.more.length >= 3) {
          this.$message({
            message: '',
            type: 'warning'
          });
          return;
        }

        for (var i = 0; i < obj.more.length; i++) {
          // 
          if (value == obj.more[i]) {
            this.$message({
              message: '',
              type: 'warning'
            });
            return;
          }
        }
        obj.more.unshift(value);

        // 
        this.API.addStuContact({
          stuId: this.stuId,
          type: index,
          contact: value
        })
          .then(res => {

          }).catch(err => {
          console.log(err.message)
        });
        //
        this.dialogFormVisible = false;
      }
    }
  }
</script>

<style lang="scss" scoped>
  .contacts {
    width: 100%;
    overflow: hidden;
    .item {
      float: left;
      height: 95px;
      border-radius: 8px;
      background-size: 100% 100% !important;
      width: calc(20% - 20px);
      margin-left: 20px;
      padding: 25px 0px 0px 20px;
      cursor: pointer;
      position: relative;
      &.back_tel, &.back_tel2 {
        &:hover {
          .playcall {
            display: block !important;
          }
        }
      }
      strong {
        color: #fff;
      }
      .warp {
        margin-bottom: 10px;
        height: 25px;
        .left {
          float: left;
          h1 {
            font-size: 18px;
            font-weight: 500;
            opacity: .8;
            color: #fff;
            float: left;
          }
          .next {
            float: left;
            width: 18px;
            height: 18px;
            background: rgba(0, 0, 0, .4);
            border-radius: 50%;
            color: #fff;
            text-align: center;
            line-height: 18px;
            font-size: 12px;
            box-shadow: 0px 0px 1px rgba(255, 255, 255, 0.2);
            margin-left: 10px;
            text-indent: 1px;
            font-weight: bold;
            cursor: pointer;
            &:hover {
              background: rgba(0, 0, 0, .25);
            }
          }
        }
        .playcall {
          float: right;
          margin-right: 10px;
          margin-top: -5px;
          .button {
            background: rgba(0, 0, 0, .4);
            color: #fff;
            border: none;
            &:hover {
              background: rgba(0, 0, 0, .25);
            }
            &.el-button--small {
              padding: 9px 7px;
            }
          }
        }
      }
      .login {
        position: absolute;
        left: 0;
        top: 0;
        background: rgba(0, 0, 0, .5);
        width: 100%;
        height: 100%;
        color: #fff;
        text-align: center;
        p {
          margin: 25px 0px 10px 0;
        }
      }
      &.back_tel {
        background: url(/static/img/workbench/back_tel.png);
        &.period {
          background: url(/static/img/workbench/back_tel_hui.png);
        }
      }
      &.back_tel2 {
        background: url(/static/img/workbench/back_tel2.png);
        &.period {
          background: url(/static/img/workbench/back_tel2_hui.png);
        }
      }
      &.back_wexin {
        background: url(/static/img/workbench/back_wexin.png);
        &.period {
          background: url(/static/img/workbench/back_wexin_hui.png);
        }
      }
      &.back_qq {
        background: url(/static/img/workbench/back_qq.png);
        &.period {
          background: url(/static/img/workbench/back_qq_hui.png);
        }
      }
      &.back_add {
        background: url(/static/img/workbench/back_add.png);
      }
      &.back_add_hover {
        background: url(/static/img/workbench/back_add_hover.png);
      }
    }
    .addAlert {
      text-align: center;
    }
  }

  @media (min-width: 1400px) {
    .contacts {
      .item {
        height: 130px;
        .login {
          p {
            margin: 40px 0px 10px 0;
          }
        }
      }
    }
  }
</style>



