<template>
  <el-row v-if="callList && callList.length" :gutter="20" style="padding:0px 15px;">
    <el-col :span="12" v-for="(item, key) in (callList && callList.length ? callList : [1])" :key="key">
      <el-card class="follow-item">
        <div class="follow-hd clearfix" slot="header">
          <h3>{{ item.userName || none }}</h3>
          <p>{{item.followTime}}</p>
        </div>
        <ul class="follow-bd">
          <li>
            <strong></strong>
            <p>{{ '' || none }}</p>
          </li>
          <li >
            <strong></strong>
            <p>{{ item.duration || none }}</p>
          </li>
          <li>
            <strong></strong>
            <p v-if="item.url">
              <el-button class="mr10" type="text" @click="playAudio"></el-button>
              <audio :src="item.url" ref="player" controls="controls" preload hidden />
              <a class="btn-download" :href="item.url" target="_blank"></a>
            </p>
            <p v-else></p>
          </li>
          <li>
            <strong></strong>
            <p>{{item.remark || none }}</p>
          </li>
        </ul>
      </el-card>
    </el-col>
  </el-row>
  <div class="follow-empty" v-else></div>
</template>

<script>
export default {
  props : ['callList'],
  data(){
    return {
      none: ''
    }
  },
  watch:{ },
  methods:{
    playAudio(evt) {
      //
      let targetElement = evt.target;
      if(targetElement.tagName!=='SPAN') return;
      let audio = targetElement.parentElement.parentElement.children[1];
      let handle=function () {
        //load
        audio.load();
        targetElement.innerText = '';
      };
      audio.removeEventListener("ended", handle, false);
      audio.addEventListener('ended', handle, false);
      if (audio.paused) {
        audio.play();// 
        targetElement.innerText = ''
      } else {
        audio.pause();// 
        targetElement.innerText = ''
      }
    },
  }
}
</script>

<style lang="scss" scoped>
  .btn-download {
    color: #409EFF;
  }
</style>



